import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { AlbumService } from '../../angular-services/album.service';

@Component({
  selector: 'app-new-album-form-admin',
  templateUrl: './new-album-form.component.html',
  styleUrls: ['./new-album-form.component.css']
})
export class NewAlbumFormComponent implements OnInit {

  formNewAlbum: FormGroup;
  alert: any = { type: '', message: '', show: false };

  constructor(private albumService: AlbumService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formNewAlbum = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required)
    });
  }

  save() {
    if (this.formNewAlbum.valid) {
      let data = this.formNewAlbum.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.albumService.addAlbum(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

}
