import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewAlbumPageComponent } from './new-album-page.component';

const routes: Routes = [
  { path: '', component: NewAlbumPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewAlbumPageRoutingModule { }
